body {
  background-color: #e4d3d3;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.breed-select {
  width: 100%;
  border: 2px solid #ccc;
  border-radius: 4px;
  margin-top: 20px;
  margin-bottom: 30px;
  padding: 10px;
  font-size: 16px;
}

.cat-info {
  color: #000;
  background-color: bisque;
  border-radius: 5px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  padding-top: 0;
  display: flex;
  box-shadow: 0 2px 6px rgba(0, 0, 0, .15);
}

.cat-info-item {
  max-width: 300px;
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 4px;
  flex: 300px;
  margin-bottom: 30px;
  padding: 20px;
}

.cat-info-item img {
  max-width: 100%;
  height: auto;
  border-radius: 4px;
  margin-bottom: 10px;
}

.loader, .error {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 30px;
}

.error {
  color: red;
}

.desc {
  max-width: 600px;
  margin: 0 auto 30px;
  line-height: 1.5;
}

img {
  margin-top: 30px;
}

.description {
  margin-bottom: 10px;
}

